            box-shadow: 0 0 0 rgba(0, 0, 0, 0.2), 0 0 0 rgba(13, 13, 13, 0.2);
    background: #B7DCB5;
    border: 0 solid #cfd8dc;
    border-radius: 5px; }
  [type='range']::-webkit-slider-thumb {
